import { flexbox } from 'styled-system';
import styled from 'styled-components';

import Box from './Box';

import asForward from './utils/asForward'

let Flex = styled(Box)`
  ${flexbox}
`;

Flex.defaultProps = {
  display: 'flex',
};

Flex.displayName = 'Flex';

Flex = asForward(Flex);

Flex.HStack = styled(Flex)`
> :not(style) ~ :not(style) {
  margin-left: ${props => props.spacing || '0.5em'};
}
`

Flex.VStack = styled(Flex)`
> :not(style) ~ :not(style) {
  margin-top: ${props => props.spacing || '0.5em'};
}
`

Flex.VStack.defaultProps = {
  flexDirection: 'column',
}

export default Flex;
